$primary: #0000B3;
$secondary: #000080;

.header-container{
    position:relative;
    width: 100%;
    height: 10vh;
    border-bottom: solid $primary;
    div{
        position: absolute;
        left: 5%;
        bottom:25%;
        p{
            font-weight: bolder;
            font-size: 2.5vw;
            color: $primary;
            text-shadow: 1px 1px 1px black;
        }
    }
    span{
        position: absolute;
        bottom: 0%;
        right: 0%;
        width: 25%;
        display: flex;
        text-align: center;
        a{
            display: block;
            width: 25%;
            text-decoration: none;
            padding: 1%;
            font-size: 1.5vw;
            transition: 0.5s;
            &:hover{
                color: white;
                background-color: $primary;
            }
           
        }
    }
}

.active{
    color: white;
    background-color: $primary;
}