$primary: #0000B3;
$secondary: #000080;

.landing-container{
   
}

.seperator{
    max-height: 10vh;
    display: block;
    color: $primary;
    text-shadow: 2px 2px 0px black;
    padding: 5vh 0 5vh 0;
    font-weight: 900;
    font-size: 4vw;
    width: 100%;
    text-align: center;
}

.banner-container{
    position: relative;
    width: 100%;
    height: 70vh;
    background-color: $primary;
    .shape1{
        position: absolute;
        left:0 ;
        display: block;
        height: 100%;
        background-color: white;
        animation: growIn 1s forwards;
    }
    .shape2{
        height: 100%;
        position: absolute;
        background-color: white;
        border-radius: 100%;
        animation: growIn2 2s forwards ;

    }
    div{
        z-index: 2;
        max-width:50%;
        position: absolute;
        right: 0;
        margin-top: 5%;
        text-shadow: 1px 5px 5px $secondary;
        h1{
            font-size: 4vw ;
            color: white;
            margin-bottom: 5%;
        }
        h2{
            color: white;
            font-size: 1.8vw;
            margin-top: 2%;
            margin-right: 3%;
            text-align: right;
        }
    }
    button{
        position: absolute;
        font-size: 2vw;
        font-weight: bolder;
        padding: 3% 6% 3% 6%;
        background-color: white;
        color: $primary;
        border: none;
        right:10%;
        top: 110%;
        transition: 0.1s;
        &:hover{
            transform: scale(1.2,1.2);
            color: white;
            background-color: transparent;
            border: solid white;
            
        }
        &:active{
            transform: scale(0.8,0.8);
            color: $primary;
            background-color: white;
            border: none;
        }
    }
}

.landing-summary-container{
    width: 100%;
    display: flex;
    justify-content: space-around;
    font-weight: bolder;
    
    div{
        width: 25%;
        height: auto;padding-bottom: 10%;
        padding-bottom: 10%;
        position: relative;
        background-color: $primary;
        color: white;
        border-radius: 5%;
        text-shadow: 5px 5px 0px black;
        h1{
            color: white;
            margin-top:2.5%  ;
            height: 15%;
            font-size: 2vw; 
            padding:0.5%; 
            text-align: center;
        }
        ul{margin-top: 8%;}
        li{
            margin-top: 3%;
            padding: 2% 5% 2% 5%;
            font-size: 1vw;
            text-align: center;
        }
        button{
            position: absolute;
            bottom: 5%;
            left: 20%;
            background-color: $primary;
            color: white;
            border: solid white;
            font-size: 2vw;
            display: block;
            width: 60%;
            padding: 2.5% 0 2.5% 0;
            transition: 0.5s;
            &:hover{
                transform: scale(1.1,1.1);
                background-color: white;
                color: $primary;
            }
            &:active{
                transform: scale(0.9,0.9);
            }
        }
    }
}

.landing-testimonials-container{
    width: 100%;
    height: 30vh;
    position: relative;
    overflow-x: hidden;
    color: white;
    text-shadow: 5px 5px 0px black;
    #frame{
    position: absolute;
    display: flex;
    overflow-x: hidden;
    transition: 3s;
    }
    span{
        background-color:$primary;
        display: block;
        width: 100vw;
        span{
            display: flex;
            justify-content: center;
            text-align: center;
            h1{
                font-size: 2.5vw;
            }
            h2{
                padding: 1%;
                font-size: 1.7vw;
            }
        }
        p{
            padding: 2% 5% 5% 5%;
            max-width: 100%;
            font-size: 1.5vw;
        }
    }
}

@keyframes growIn {
    0%{
        width: 0%;
    }
    100%{
        width: 40%;
    }
}

@keyframes growIn2 {
    0%{
        left: 0%;
        width: 0%;
    }
    50%{
        left: 32%;
        width: 15%;
        
    }
    100%{
        left: 32%;
        width: 15%;
        
    }
}